.owl-item {
  min-width: 393px;
}

.owl-theme {

   .item-book {
    display: block;
    background: url("../images/bg_carousel_item.png") no-repeat;
    background-size: 100% 100%;
    height: 440px;

    & h3 {
      margin: 0;
      padding: 36px 41px 0 36px;
      font-family: 'Roboto', sans-serif;
      font-size: 23px;
      font-weight: 400;
      color: #000;

      &:hover {
        color: #cc2643;
      }

    }

    & p {
      margin: 0;
      padding: 30px 41px 0 36px;
      font-family: 'Roboto', sans-serif;
      font-size: 14px;
      font-weight: 300;
      color: #494949;
    }

    .bookmark-box {
      position: absolute;
      left: 345px;
      width: 19px;
      height: 34px;
      background-color: #ededed;
      margin: 2px 20px 0 0;
      z-index: 2;

      &_yellow {
        background-color: #ffcc00;
      }

      &:after {
        position: absolute;
        top: 25px;
        right: 0;
        bottom: 100%;
        left: auto;
        margin-right: 0;
        border-top: none;
        border-right: 10px solid transparent;
        border-bottom: 10px solid #fff;
        border-left: 10px solid transparent;
        content: "";
      }

    }

     .tag-crumbs {
       display: block;

       &_main-page {
          padding: 30px 41px 0 36px;
       }

       &__list {
         text-align: left;
         list-style: none;
         margin: 0;
         padding: 0;

         li {
           margin: 0;
           padding: 0;
           display: inline-block;
           line-height: 29px;
         }

         &_link {
           font-family: 'Roboto', sans-serif;
           font-size: 10px;
           font-weight: 700;
           text-transform: uppercase;
           white-space: nowrap;
           border-radius: 2px;
           padding: 5px 10px;
           color: #343434;
           background-color: #fff;
           border: solid 1px #ccc;
           letter-spacing: 1px;
         }

       }

     }


     &__hover-box {
       position: relative;
       display: none;
       top: -277px;
       left: 2px;
       border-radius: 5px;
       background-color: rgba(255,255,255,0.95);
       width: 374px;
       height: 430px;
       z-index: 1;
       text-align: center;

       &__add-bookmark {
         display: inline-block;
         margin-top: 37px;
         font-family: 'Roboto', sans-serif;
         font-weight: 300;
         font-size: 20px;
         color: #000;
         text-decoration: none;
         border-bottom: 1px dashed #646464;

         &:hover {
           color: #e8290c;
           border-color: #e8290c;
         }

       }

       &__add-reservation {
         display: inline-block;
         margin-top: 86px;
         font-family: 'Roboto', sans-serif;
         font-weight: 500;
         font-size: 26px;
         color: #000;
         text-shadow: 1px 1px 0px rgba(255, 255, 255, 0.5);
         text-decoration: none;
         background-color: #ffcc00;
         padding: 18px 58px;
         border-radius: 40px;
         box-shadow:
           0 0 0 4px #fff,
           0 0 0 5px #fd0;

         &:hover {
           text-shadow: none;
           color: #fff;
           background-color: #e8290c;
           box-shadow:
             0 0 0 4px #fff,
             0 0 0 5px #e8290c;
         }

       }

       &__info {
         display: inline-block;
         margin-top: 20px;
         font-family: 'Roboto', sans-serif;
         font-weight: 300;
         font-size: 20px;
         color: #000;
         text-decoration: none;
         border-bottom: 1px solid #646464;

         &:hover {
           color: #e8290c;
           border-color: #e8290c;
         }
       }

       & hr {
         margin-top: 75px;
         border-style: none none solid none;
         border-width: 1px;
         border-color: rgba(176, 176, 176, 0.50);
       }

       & span {
         position: relative;
         display: inline-block;
         left: 0%;
         bottom: 22px;
         font-family: 'Roboto', sans-serif;
         font-weight: 300;
         font-size: 20px;
         color: #afafaf;
         background-color: #fff;
         padding: 0 6px;
       }

       &__share-box {
          display: block;

         &__link {
           position: relative;
           top: -15px;
           display: inline-block;
           width: 65px;
           height: 65px;
           margin: 0 5px;
           border: 1px dashed #d9caaf;
           border-radius: 50%;
           color: #cc2643;

           &:hover {
             color: #e8290c;
             border-color: #e8290c;
           }

           & img {
             position: relative;
             left: 0;
             top: 30%;
             width: 25px;
             height: 25px;
           }

         }
//         & share {
//           fill: #c50000;
//         }

       }

     }

     &:hover .item-book__hover-box {
       display: block;
     }

     .animated {
       -webkit-animation-duration: 0.1s;
       animation-duration: 0.1s;
       -webkit-animation-fill-mode: both;
       animation-fill-mode: both;
       -webkit-animation-timing-function: linear;
       animation-timing-function: linear;
     }

     @-webkit-keyframes fadeIn {
       0% {opacity: 0;}
       100% {opacity: 1;}
     }
     @keyframes fadeIn {
       0% {opacity: 0;}
       100% {opacity: 1;}
     }
     .fadeIn {
       -webkit-animation-name: fadeIn;
       animation-name: fadeIn;
     }


  }

}

.tab-container {
  display: block;
  min-width: 1150px;
  background-color: #f6f6f6;

  &__tabs {
    position: relative;
    display: block;
    list-style-type: none;
    padding: 77px 0 57px 0;
    margin: 0;
    text-align: center;
    z-index: 1;
  }

  &__tabs li {
    display: inline-block;
    margin: 0 15px;
    font-size: 30px;
    font-family: "RobotoSlabLight";
    font-weight: 300;
    color: #363636;
    text-decoration: none;
    border-bottom: 1px dashed #646464;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;

    &:hover {
      color: #e8290c;
      border-color: #e8290c;
    }

    &:active {

    }

    &.active {
      cursor: default;
      border-bottom: none;
      font-family: "RobotoSlabRegular";
      color: #000;
      font-weight: 500;

      &:hover {
        color: #000;
      }

    }

  }

  &__tab {
    position: relative;
    display: block;
    z-index: 0;
    left: -1px;
    list-style-type: none;
    margin: 0;
    padding-left: 10px;
  }

  &__tab li {
    display: none;
  }

  &__tab li.active {
    display: block;
  }

  .btn-box {
    display: block;
    width: 100%;
    text-align: center;
    padding: 10px 0 70px 0;

    .btn-show-all-owl {
      position: relative;
      display: inline-block;
      font-family: 'Roboto', sans-serif;
      font-size: 18px;
      font-weight: 300;
      color: #fff;
      padding: 12px 30px;
      text-decoration: none;
      background-color: #1d609b;
      border-radius: 5px;

      &:hover {
        background-color: #e8290c;
      }

      &:active {
        background: #d0250c;
      }

    }

  }

}

.news-box {

