#map {
  background-color: #fff;
  width: 100%;
  min-width: 1150px;
  height: 840px;

  .map-box {
    position: absolute;
    right:0;
    margin: 30px;
    width: 490px;
    height: 785px;
    z-index: 1;
    background-color: #fff;
    border-radius: 5px;
    -webkit-box-shadow: 0 6px 12px rgba(0,0,0,.175);
    box-shadow: 0 6px 12px rgba(0,0,0,.175);

    &__list-bib {
      height: 450px;
      margin: 10px 10px 0 10px;
      overflow-y: auto;
      border: 1px solid #f2f2f2;
      border-radius: 2px;

      &__item {
        border-bottom: 1px solid #f2f2f2;
        border-radius: 2px;
        padding: 25px;
        font-family: 'Roboto', sans-serif;
        font-size: 18px;
        font-weight: 400;
        line-height: 26px;

        &__link {
          color: #000;
          text-decoration: underline;
        }

        & span {
          color: #d1d1d1;
        }

        &:hover .map-box__list-bib__item__link, &:hover span {
          color: #e8290c;
        }

        &:last-child {
          border-bottom: none;
        }

        &__btn-box {
          display: block;
          margin: 10px 0;
        }

        &__btn {
          display: inline-block;
          font-weight: 300;
          font-size: 14px;
          color: #fff;
          padding: 3px 15px;
          position: relative;
          text-decoration: none;
          background-color: #1d609b;
          border-radius: 4px;

          &:hover {
            background-color: #e8290c;
          }

          &:active {
            background: #d0250c;
          }

        }

      }

      &__detail {

        &__btn-back {
          display: inline-block;
          font-family: 'Roboto', sans-serif;
          font-weight: 300;
          font-size: 14px;
          color: #fff;
          padding: 3px 15px;
          position: relative;
          text-decoration: none;
          background-color: #1d609b;
          border-radius: 4px;
          margin: 30px 0 0 30px;

          &:hover {
            background-color: #e8290c;
          }

          &:active {
            background: #d0250c;
          }
        }

        &__title {
          margin: 30px 30px 0 30px;
          font-family: "RobotoSlabBold";
          color: #1b1b1b;
          font-weight: 500;
          font-size: 30px;
        }

        &__title-info {
          margin: 0;
          padding: 30px;
          font-family: 'Roboto', sans-serif;
          font-size: 18px;
          font-weight: 400;
          line-height: 26px;
          border-bottom: 1px solid #f2f2f2;

          & a {
            color: #000;
            text-decoration: underline;

            &:hover {
              color: #e8290c;
            }

          }
        }

      }

    }

    &__title {
      display: block;
      margin: 38px 10px 20px 10px;
      font-family: "RobotoSlabRegular";
      color: #363636;
      font-weight: 500;
      font-size: 17px;
    }

    .abc-crumbs {
      display: block;
      margin: 10px;

      &__list {
        list-style: none;
        margin: 0;
        padding: 0;

        li {
          margin: 0;
          padding: 0;
          display: inline-block;
          line-height: 29px;
          text-align: center;
        }

        &_link {
          display: inline-block;
          width: 69px;
          height: 69px;
          padding-top: 18px;
          margin: 0 5px 10px 0;
          background-color: #fff;
          font-family: "RobotoSlabRegular";
          color: #363636;
          font-weight: 500;
          font-size: 36px;
          user-select: none;
          text-decoration: none;
          border: 1px #e5e5e5 solid;
          border-radius: 2px;
          white-space: nowrap;

          &-img {
            display: inline-block;
            width: 69px;
            height: 69px;
            padding-top: 5px;
            margin: 0 5px 10px 0;
            background-color: #fff;
            border: 1px #e5e5e5 solid;
            border-radius: 2px;

            img {
              position: relative;
              top: 10px;
              width: 40px;
              height: 40px;
            }

            &:hover {
              color: #e8290c;
              border-color: #e8290c;
            }


          }

          &:hover {
            color: #e8290c;
            border-color: #e8290c;
          }

        }

      }

    }


  }

}

.facets-container {
