.bg {
  min-width: 1150px;

  &_first {
    background: url("../images/bg.jpg") no-repeat #aa863f;
    background-size: 1900px;
    background-position: 50% 0%;
  }

  &_second {
    background: url("../images/bg_blue.jpg") no-repeat #57a4db;
    background-size: 100% 100%;
    background-position: 50% 50%;
  }

  &__hr {
    margin: 0;
    padding: 0;
    border-style: none none solid none;
    border-width: 1px;
    border-color: rgba(255, 255, 255, 0.3);
  }

  h1 {
    display: block;
    margin: 0;
    padding: 250px 40px 0 40px;
    font-family: "RobotoSlabLight";
    font-weight: 300;
    font-size: 50px;
    line-height: 62px;
    color: #fff;

    a {
      text-decoration: none;
      color: #fff;
      border-bottom: solid 1px #fff;

      &:hover {
        color: #e8290c;
        border-bottom-color: #e8290c;
      }

    }

    sup {
      position: relative;
      top: -30px;
      font-size: 11px;
    }

  }

  &__black {
    background-color: rgba(0,0,0,0.5);
  }

}

.menu-box {
  display: block;
  margin: 0;
  list-style: none;
  font-family: 'Roboto', sans-serif;
  font-weight: 400;

  &_first {
    padding: 40px 0px 40px 40px;
  }

  &_second {
    padding: 30px 0px 30px 40px;
  }

  li {
    display: inline-block;
    margin-right: 40px;

    &:last-child {
      margin-right: 0;
    }

    & .header-link {
      position: relative;
      font-size: 22px;
      text-decoration: none;
      color: #fff;
      border-bottom: solid 1px #fff;

      &_active {
        border-bottom: none;
      }

      &_active:after {
        position: absolute;
        top: 47px;
        right: auto;
        bottom: 100%;
        left: 50%;
        margin-left: -10px;
        border-top: none;
        border-right: 10px solid transparent;
        border-bottom: 10px solid #fff;
        border-left: 10px solid transparent;
        content: "";
      }

      &:hover {
        color: #e8290c;
        border-bottom-color: #e8290c;
      }

    }

    & .footer-link {
      font-size: 22px;
      text-decoration: none;
      color: #000;
      border-bottom: solid 1px #000;

      &:hover {
        color: #e8290c;
        border-bottom-color: #e8290c;
      }

    }

  }

}

.search-box {
  margin: 0 auto;
  min-width: 900px;
  max-width: 1165px;

  &_first {
    padding: 60px 0px 280px 0px;
  }

  &_second {
    padding: 30px 0px 20px 0px;
  }

  .input-form-group {
    display: table-cell;
    vertical-align: middle;
    width: 1%;
  }
}

.search-form {
  position: relative;
  display: table;
  width: 100%;
  border-radius: 3px;
  border-collapse: separate;

  &__select {
    display: table-cell;
//    height: 80px;
    padding: 0 15px;
    outline: none;
    cursor: pointer;
    border: 1px #eee solid;
    border-right-color: #eee;
    border-radius: 5px 0 0 5px;
    -moz-appearance: none;
    -webkit-appearance: none;
    appearance: none;
    background: url('../images/selectdown.svg') no-repeat 95% 50%;
    background-size: 13px;
    background-color: #fff;

    color: #000;
    font-family: 'Roboto', sans-serif;
    font-size: 14px;
    font-weight: 300;

    &:focus {
      border: solid 1px #2196F3;
    }
  }

  &__input {
    display: table-cell;
    position: relative;
    float: left;
    width: 100%;
    height: 80px;
    padding: 0 30px;
    vertical-align: middle;
    outline: none;
    color: #000;
    font-family: 'Roboto', sans-serif;
    font-size: 50px;
    font-weight: 300;
    border: 1px #eee solid;
    border-left-color: #fff;
    border-right-color: #fff;
    background-color: #fff;
    border-radius: 5px 0 0 5px;

    &:focus {
//      border: solid 1px #e8290c;
    }

  }

  &__submit {
    display: table-cell;
    position: relative;
    padding: 15px 30px 15px 10px;
    height: 80px;
    background-color: #fff;
    color: #fff;
    font-size: 0.875em;
    font-weight: 500;
    border: 1px #fff solid;
    border-radius: 0 5px 5px 0;
    outline: none;

    img {
      width: 41px;
      height: 41px;

      &:hover #search_img {
        fill: #e8290c;
      }

    }

  }

}

.select_lib {
  display: inline-block;
  font-family: 'Roboto', sans-serif;
  font-weight: 300;
  font-size: 18px;
  color: #fff;
  padding: 30px 0 0 40px;

  & a {
    text-decoration: none;
    color: #fff;
    border-bottom: dashed 1px #fff;
    margin-left: 10px;

    &:hover {
      color: #e8290c;
      border-bottom-color: #e8290c;
    }
  }

  & a.margin-none {
    margin-left: 0;
  }

}

.search-btn {
  position: relative;
  display: inline-block;
  padding: 20px 0 0 20px;
  vertical-align: middle;
  color: #fff;
  font-family: 'Roboto', sans-serif;
  font-weight: 300;
  font-size: 18px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;

  &__radio {
    position: relative;
    display: inline-block;
    margin: 0 5px;
    text-align: center;
    white-space: nowrap;
    vertical-align: middle;
    border-bottom: 1px #fff dotted;
    cursor: pointer;
    text-decoration: none;
    color: #fff;

    &:hover {
      color: #e8290c;
      border-bottom-color: #e8290c;
    }

  }

  &__link {
    position: relative;
    display: inline-block;
    margin: 0 5px;
    text-align: center;
    white-space: nowrap;
    vertical-align: middle;
    border-bottom: 1px #fff dotted;
    cursor: pointer;
    text-decoration: none;
    color: #fff;

    &_mygeo {
      position: relative;
      top: 2px;
      margin-left: 10px;

      & img {
        width: 15px;
        height: 15px;
      }

    }

    &:hover {
      color: #e8290c;
      border-bottom-color: #e8290c;
    }

  }

  & input[type="radio"] {
    display: none;
  }

  & input[type=radio]:checked + .search-btn__radio {
    color: #fff;
    padding: 3px 12px;
    background-color: rgba(27,20,100,0.3);
    border-radius: 15px;
    border-bottom: none;
    cursor: default;
  }

}

.search-btn_abc-list {
  padding-left: 40px;
}

.search-in-result {
  position: relative;
  display: inline-block;
  padding: 20px 0 0 0;
  vertical-align: middle;
  color: #fff;
  font-family: 'Roboto', sans-serif;
  font-weight: 300;
  font-size: 18px;
  float: right;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;

  label {
    cursor: pointer;

    & input[type=checkbox] {
      background-color: #fff;
    }

    &:hover {
      color: #e8290c;
      border-bottom-color: #e8290c;
    }

  }

}

.my_address {
  display: inline-block;
  float: right;
  font-family: 'Roboto', sans-serif;
  font-weight: 300;
  font-size: 18px;
  color: rgba(255,255,255,0.5);
  padding: 30px 40px 0 0;
}