.cal {
  position: relative;
  padding: 4px;
  border-radius: 5px;
  display: inline-block;
  vertical-align: baseline;
  zoom: 1;
  *display: inline;
  *vertical-align: auto;
}
.cal:before {
  content: '';
  position: absolute;
  bottom: 3px;
  left: 4px;
  right: 4px;
  height: 6px;
}
.cal a {
  text-decoration: none;
  cursor: pointer;
}
.cal tr:first-child td {
  border-top: 0;
}
.cal td:first-child {
  border-left: 0;
}
.cal tr:first-child a {
  border-top: 0;
  margin-top: 0;
}
.cal tr:last-child a {
  border-bottom: 0;
  margin-bottom: 0;
}
.cal td:first-child a {
  border-left: 0;
  margin-left: 0;
}
.cal td:last-child a {
  border-right: 0;
  margin-right: 0;
}
.cal tr:last-child td:first-child a {
  border-radius: 0 0 0 3px;
}
.cal tr:last-child td:last-child a {
  border-radius: 0 0 3px 0;
}

.cal-table {
  position: relative;
  margin: 0 0 0 35px;
  border-collapse: separate;

  font-family: 'Roboto', sans-serif;
  font-weight: 400;
  font-size: 20px;

  &__margin-news {
    margin: 0;
  }

  &__date {
    float: left;
    font-weight: 300;
    font-size: 20px;
    color: #000;
    margin: 10px 0 0 15px;
  }

  & select {
    float: left;
    margin: 20px 10px 20px 15px;
    width: 150px;
    border: 1px solid #e5e5e5;
    border-radius: 5px;
    padding: 10px;
    font-size: 14px;
    margin-right: 5px;
    background-color: #fff;
  }

}

.cal-caption {
  width: 100%;
  padding-bottom: 1px;
  line-height: 50px;
  text-align: center;
  text-transform: uppercase;
}
.cal-caption a {
  line-height: 50px;
  padding: 0 10px;
  font-size: 20px;
  font-weight: normal;
}
.cal-caption .prev {
  float: left;
}
.cal-caption .next {
  float: right;
}

.cal-body td {
  width: 50px;
}
.cal-body a {
  display: block;
  position: relative;
  line-height: 50px;
  color: #4d5155;
  text-align: center;
}
.cal-body a:hover {
  margin: -1px;
  color: #e8290c;
  border: 1px solid #e8290c;
  border-radius: 50px;
}

.cal-off a {
  color: #ccc;
}

.cal-today a {
  background: #cee3f5;
  border-radius: 50px;
}

.cal-selected a, .cal-body a:active {
  margin: -1px;
  color: #fff;
  background: #e8290c;
  border-radius: 50px;
}

.cal-check a {
  color: #e8290c;
}
