.result-container {
    border-top: 1px solid #f2f2f2;
}

.result {
  display: table;
  padding-bottom: 30px;
  border-bottom: 1px solid #f2f2f2;

  &__index {
    display: table-cell;
    white-space: nowrap;
    font-family: 'Roboto', sans-serif;
    font-weight: 300;
    font-size: 14px;
    color: #b2b2b2;
  }

  &__info {
    display: table-cell;
    padding-left: 20px;
  }

  &:last-child {
    border-bottom: none;
  }


  h3 {
    margin: 30px 15px 0 0;
    padding: 0;
    font-family: 'Roboto', sans-serif;
    font-weight: 500;
    font-size: 24px;
    line-height: 34px;

    a {
      display: block;
      text-decoration: underline;
      margin-bottom: 5px;
      color: #23589e;

      &:hover {
        color: #e8290c;
      }
    }

  }

  &__author {
    display: block;
    margin: 15px 15px 0 0;
    font-family: 'Roboto', sans-serif;
    font-weight: 500;
    font-size: 14px;
    color: #585858;
  }

  &__detail {
    display: table;
    margin-top: 15px;
    line-height: 22px;
    font-family: 'Roboto', sans-serif;
    font-weight: 500;
    font-size: 14px;
    color: #636363;

    &_box {
      display: table-cell;
      vertical-align: top;
    }

    &_row {
      display: table-row;
    }

    &_img {
      width: 100px;
      max-width: 100px;
      max-height: 200px;
      padding: 5px;
      margin-right: 10px;
      border: 1px solid #eee;
      border-radius: 0;
    }

    &_title {
      white-space: nowrap;
    }

    &_description {
      display: block;
      margin-left: 10px;
    }

    &__print {
      display: block;
      text-decoration: underline;
      color: #23589e;
      font-family: 'Roboto', sans-serif;
      font-weight: 500;
      font-size: 14px;

      &:hover {
        color: #e8290c;
      }
    }

  }

  &__crumbs {

    &__list {
      display: block;
      margin: 15px 15px 0 0;
      list-style: none;
      padding: 0;

      li {
        margin: 0;
        padding: 0;
        display: inline-block;
        text-align: center;
      }

      &_link {
        display: inline-block;
        padding: 5px 10px;
        margin: 0 0 5px 0;
        font-family: 'Roboto', sans-serif;
        font-weight: 400;
        color: #000;
        font-size: 11px;
        user-select: none;
        text-decoration: none;
        text-transform: uppercase;
        background-color: #e8f1fb;
        border-radius: 2px;
        white-space: nowrap;

        &:hover {
          color: #fff;
          background-color: #e8290c;
        }

      }

    }

  }

}

.search-result-hr {
  margin: 0;
  border-style: none none solid none;
  border-width: 1px;
  border-color: #f2f2f2;
}

.sort_select {
  display: inline-block;
  float: right;
  margin: 8px;
  vertical-align: middle;
  font-family: 'Roboto', sans-serif;
  font-weight: 400;
  font-size: 14px;
  color: #4c4c4c;

  select {
    padding: 10px;
    outline: none;
    cursor: pointer;
    border: 1px #d1d0e0 solid;
    border-radius: 5px;
    -moz-appearance: none;
    -webkit-appearance: none;
    appearance: none;
    background: url('../images/selectdown.svg') no-repeat 95% 50%;
    background-size: 14px;
    background-color: #fff;
    color: #000;
  }

}