.logo {
  display: table;
  float: left;

  &__img {
    padding: 20px 0;
  }

  &__text {
    display: table-cell;
    vertical-align: middle;
    color: #000;
    font-family: 'Roboto', sans-serif;
    font-size: 19px;
    font-weight: 300;
    padding-left: 12px;
    cursor: pointer;

    span {
      font-weight: 500;
    }

    &:hover {
      color: #ec2700;
    }

  }

}

.language{
  display: inline-block;
  margin: 21px 0 0 30px;

  &__btn {
    display: inline-block;
    font-family: 'Roboto', sans-serif;
    font-size: 14px;
    font-weight: 300;
    color: #000;
    margin: 15px 9px;
    border-bottom: 1px #000 dotted;
    text-decoration: none;

    &:hover {
      color: #e8290c;
    }

    &_active {
      color: #000;
      margin: 0;
      padding: 15px 9px;
      background-color: #f0f7fd;
      border-radius: 2px;
      border-bottom: none;
    }

  }

}

.user-block {
  display: table;
  float: right;
  margin: 25px 0 21px 40px;

  &__info {
    display: table-cell;
    white-space: nowrap;
    font-family: 'Roboto', sans-serif;
    font-size: 18px;
    color: #000;
    border-style: solid none solid solid;
    border-width: 1px;
    border-color: #e5e5e5;
    border-radius: 5px 0 0 5px;

    &-key {
      position: relative;
      display: inline-block;
      top: 5px;
      left: 19px;
      width: 23px;
      height: 23px;
    }

    &-text {
      display: inline-block;
      font-weight: 300;
      padding: 5px 0 5px 29px;
    }

    &-name {
      display: inline-block;
      font-weight: 500;
      padding: 5px 19px 5px 5px;
    }

  }
  &__btn {
    display: table-cell;
    white-space: nowrap;
    font-family: 'Roboto', sans-serif;
    font-size: 18px;
    font-weight: 300;
    color: #fff;
    background-color: #e8290c;
    padding: 5px 5px 5px 19px;
    text-decoration: none;

    &:hover {
      background: #e4290d;
    }

    &:active {
      background: #d0250c;
    }

  }

}

.click-nav {
  margin: 0;
  padding: 0;

  ul {
    position: relative;
    margin: 0;
    padding: 0;
  }

  ul li {
    position: relative;
    list-style: none;
    cursor: pointer;
  }

  ul li ul {
    position: absolute;
    top: 100%;
    left: -110px;
    z-index: 1000;
    display: none;
    float: left;
    min-width: 160px;
    padding: 5px 0;
    margin: 2px 0 0;
    font-size: 14px;
    text-align: left;
    list-style: none;
    background-color: #fff;
    -webkit-background-clip: padding-box;
    background-clip: padding-box;
    border: 1px solid #ccc;
    border-radius: 4px;
    -webkit-box-shadow: 0 6px 12px rgba(0,0,0,.175);
    box-shadow: 0 6px 12px rgba(0,0,0,.175);
  }

  ul .clicker {
    position: relative;
    font-family: 'Roboto', sans-serif;
    font-size: 18px;
    font-weight: 300;
    color: #fff;
    background-color: #e8290c;
    padding: 9px 19px 9px 19px;
    border-radius: 0 3px 3px 0;

    img {
      width: 10px;
      height: 10px;
    }

  }

  ul .clicker:hover, .click-nav ul .active {
    background: #d0250c;
  }

  ul li a {
    transition: background-color 0.2s ease-in-out;
    -webkit-transition: background-color 0.2s ease-in-out;
    -moz-transition: background-color 0.2s ease-in-out;
    display: block;
    text-decoration: none;

    font-family: 'Roboto', sans-serif;
    font-size: 14px;
    font-weight: 300;
    color: #000;
    padding: 3px 10px;
  }

  ul li a:hover {
    background: #F2F2F2;
  }

  /* Fallbacks */
//  .no-js ul {
//    display: none;
//  }
//
//  .no-js:hover ul {
//    display: block;
//  }

}
