.content-page {

  &__title {
    display: block;
    margin: 45px 10px 0 40px;
    font-family: "RobotoSlabBold";
    color: #373737;
    font-weight: 500;
    font-size: 36px;

    & span {
      font-family: "RobotoSlabLight";
      font-size: 30px;
      font-weight: 300;
      color: #000;
    }

  }

  &__text {
    font-family: 'Roboto', sans-serif;
    font-size: 18px;
    font-weight: 300;
    color: #000;
    line-height: 32px;
    margin: 20px 45px 45px 45px;

    & a {
      color: #3062a4;

      &:hover {
        color: #e8290c;
      }

    }

  }

  .side-box {
    background-color: #f0f7fd;
    padding-bottom: 40px;

    &__title {
      margin: 0;
      padding: 50px 50px 0 50px;
      font-family: "RobotoSlabBold";
      color: #373737;
      font-weight: 500;
      font-size: 30px;
    }

    &__item {
      margin: 30px 50px 0 50px;
      font-family: 'Roboto', sans-serif;
      font-size: 18px;
      font-weight: 400;
      color: #373737;
      padding-bottom: 30px;
      border-bottom: 1px solid #d8dee3;

      &:last-child {
        padding-bottom: 0;
        border-bottom: none;
      }

      & a {
        color: #3062a4;

        &:hover {
          color: #e8290c;
        }

      }

    }

    &__form {

      padding: 20px 50px 20px 50px;

      & input, & textarea {
        display: block;
        width: 100%;
        font-family: 'Roboto', sans-serif;
        font-size: 26px;
        font-weight: 300;
        color: #373737;
        padding: 12px;
        margin-bottom: 20px;
        border: 1px solid #dde8f2;
        border-radius: 5px;
      }

      & textarea {
        min-height: 300px;
      }

      &__btn {
        position: relative;
        display: inline-block;
        font-family: 'Roboto', sans-serif;
        font-size: 18px;
        font-weight: 300;
        color: #fff;
        padding: 12px 40px;
        text-decoration: none;
        background-color: #e8290c;
        border: none;
        border-radius: 5px;

        &:hover {
          background-color: #d0250c;
        }

        &:active {
          background: #c8240c;
        }

      }

    }

    &__btn__ask {
      position: relative;
      display: inline-block;
      font-family: 'Roboto', sans-serif;
      font-size: 18px;
      font-weight: 300;
      color: #fff;
      padding: 12px 40px;
      text-decoration: none;
      background-color: #e8290c;
      border: none;
      border-radius: 5px;
      margin: 30px 50px 0 50px;

      &:hover {
        background-color: #d0250c;
      }

      &:active {
        background: #c8240c;
      }

    }


    &__list-category {
      margin: 30px 50px 0 50px;
      padding: 0;
      list-style: none;
      line-height: 45px;
      font-family: 'Roboto', sans-serif;
      font-size: 18px;
      font-weight: 400;
      color: #23589e;

      & a {
        color: #23589e;

        &:hover {
          color: #e8290c;
        }

      }

      &_active {
        list-style: disc;

        & a {
          text-decoration: none;
          font-weight: 700;
        }

      }

    }

  }

}

.answer-search {
  display: table;
  padding: 20px 30px 0 40px;

  &__box {
    display: inline-block;
    margin-right: 30px;
  }

  & label {
    display: table-cell;
    padding-bottom: 10px;
    font-family: "RobotoSlabLight";
    font-size: 14px;
    font-weight: 300;
    color: #000;
  }

  & input {
    display: table-cell;
    width: 150px;
    border: 1px solid #e5e5e5;
    border-radius: 5px;
    padding: 5px;
    font-size: 14px;
    margin-right: 5px;
    height: 27px;
  }

  & button {
    border: none;
    font-family: 'Roboto', sans-serif;
    font-weight: 300;
    font-size: 14px;
    color: #fff;
    background-color: #1d609b;
    border-radius: 5px;
    margin: 5px 5px 0 0;
    padding: 5px 15px;

    &:hover {
      background-color: #e8290c;
    }

    &:active {
      background: #c8240c;
    }

  }

}

.answer-box {
  display: table;

  &__index {
    display: table-cell;
    white-space: nowrap;
    font-family: 'Roboto', sans-serif;
    font-weight: 300;
    font-size: 14px;
    color: #b2b2b2;
  }

  &__content {
    position: relative;
    display: block;
    width: 100%;
    border-radius: 5px;
    border: 1px solid #e5e5e5;

    &:before, &:after {
      content: '';
      position: absolute;
      left: -26px;
      bottom: 30px;
      border: 13px solid transparent;
      border-right: 13px solid #7f7f7f;

    }
    &:after {
      border-right: 13px solid #fff;
      bottom: 30px;
    }


      &_padding {
      display: table-cell;
      width: 100%;
      padding: 30px 30px 0 10px;
    }

    &__question{
      font-family: 'Roboto', sans-serif;
      font-weight: 400;
      font-size: 16px;
      color: #000;
      line-height: 26px;
      padding: 25px;
      margin: 0;
    }

    &__answ {
      font-family: "RobotoSlabLight";
      font-size: 14px;
      font-weight: 300;
      color: #5e5e5e;
      line-height: 24px;
      padding: 25px;
      margin: 0;
    }

    &__hr {
      border-style: none none solid none;
      border-width: 1px;
      border-color: #e5e5e5;
      margin: 0 25px 0 25px;
    }

  }

}

.news-page-box {
  font-family: 'Roboto', sans-serif;
  border-bottom: 1px solid #e5e5e5;
  padding-bottom: 30px;

  &:first-child {
    border: none;
  }

  &__date {
    margin: 30px 20px 0 40px;
    font-weight: 300;
    font-size: 12px;
    color: #a3a3a3;
  }

  &__title {
    font-weight: 400;
    font-size: 26px;
    line-height: 36px;
    margin: 20px 20px 0 40px;

    & a {
      color: #23589e;

      &:hover {
        color: #e8290c;
      }

    }

  }

  &__teaser {
    margin: 20px 20px 0 40px;
    padding: 0;
    font-weight: 300;
    font-size: 16px;
    color: #585858;
    line-height: 24px;
  }

}
