.footer {

  &-hr {
    min-width: 1150px;
    margin: 0;
    padding: 0;
    border-style: none none solid none;
    border-width: 1px;
    border-color: #ededed;
  }

  &-logo {

    &__bib {
      position: relative;
      top: -7px;
      display: inline-block;
      margin: 30px 40px 30px 0;
    }

    &__min {
      display: inline-block;
      margin: 30px 40px 30px 0;
    }

    &__ask-lib {
      position: relative;
      top: -7px;
      display: inline-block;
      margin: 20px 20px 30px 0;
      font-family: 'Roboto', sans-serif;
      font-weight: 300;
      font-size: 18px;
      color: #ed3511;
      padding: 12px 38px;
      border: 1px solid #ed3511;
      border-radius: 5px;
      text-decoration: none;

      &:hover {
        text-decoration: underline;
      }

    }

    &__ask-all {
      position: relative;
      top: -10px;
      display: inline-block;
      font-family: 'Roboto', sans-serif;
      font-weight: 300;
      font-size: 18px;
      color: #000;
      text-decoration: underline;

      &:hover {
        color: #ed3511
      }
    }

  }


}