.detail-page {

  &__title {
    display: inline-block;
    margin: 45px 10px 0 40px;
    font-family: "RobotoSlabBold";
    color: #373737;
    font-weight: 500;
    font-size: 36px;
  }

  &__options {
    display: block;
    margin-left: 40px;
    padding: 30px 0 30px 0;

    &_border-bottom {
      border-bottom: 1px solid #f2f2f2;
    }

    &_border-top {
      border-top: 1px solid #f2f2f2;
    }

    &__btn {
      position: relative;
      display: inline-block;
      font-family: 'Roboto', sans-serif;
      font-size: 14px;
      font-weight: 300;
      color: #fff;
      padding: 6px 16px;
      text-decoration: none;
      background-color: #1d609b;
      border-radius: 5px;
      margin-right: 10px;

      &:hover {
        background-color: #e8290c;
      }

      &:active {
        background: #d0250c;
      }

    }

    &__btn-atrr {
      position: relative;
      display: inline-block;
      font-family: 'Roboto', sans-serif;
      font-size: 18px;
      font-weight: 300;
      color: #3062a4;
      text-decoration: none;
      margin-right: 50px;

      & span {
        position: relative;
        margin-left: 10px;
        top: -20px;
        text-decoration: underline;
      }

      &:hover {
        color: #e8290c;
      }

    }

    &__count {
      font-family: 'Roboto', sans-serif;
      font-size: 14px;
      font-weight: 300;
      color: #999999;
    }

  }

  &__info {
    margin: 30px 0 30px 30px;
    font-family: 'Roboto', sans-serif;
    font-size: 18px;
    font-weight: 300;
    color: #000;
    line-height: 28px;

    & .marc_dump span.fieldlabel, span.subfieldlabel {
      font-family: monospace;
      color: blue;
    }

    & .marc_dump span.data, span.indicator {
      font-family: monospace;
    }

    & .marc_dump span.label {
      font-family: monospace;
    }

  }

  .tab-container {
    background-color: #fff;
    min-width: auto;

    &__tabs {
      position: relative;
      top: -5px;
      display: inline-block;
      list-style-type: none;
      margin: 0;
      padding: 0;
      text-align: center;
      z-index: 1;
    }

    &__tabs li {
      display: inline-block;
      font-family: 'Roboto', sans-serif;
      font-size: 14px;
      font-weight: 400;
      color: #000;
      margin: 5px 10px;
      border-bottom: 1px #000 dotted;
      text-decoration: none;
      cursor: pointer;
      -webkit-user-select: none;
      -moz-user-select: none;
      -ms-user-select: none;
      user-select: none;

      &:hover {
        color: #e8290c;
        border-color: #e8290c;
      }

      &:active {

      }

      &.active {
        color: #000;
        margin: 0;
        padding: 5px 10px;
        border-radius: 2px;
        border: 1px #e5e5e5 solid;

        &:hover {
          color: #000;
        }

      }

    }

    &__tab {
      position: relative;
      display: block;
      z-index: 0;
      left: -1px;
      list-style-type: none;
      margin: 0;
      padding-left: 10px;
    }

    &__tab li {
      display: none;
    }

    &__tab li.active {
      display: block;
    }

  }

}

.reserv-box {
  background-color: #f0f7fd;
  padding-bottom: 40px;

  &__title {
    padding: 50px 10px 0 50px;
    font-family: "RobotoSlabBold";
    color: #373737;
    font-weight: 500;
    font-size: 30px;

    & span {
      font-family: "RobotoSlabLight";
      font-size: 30px;
      font-weight: 200;
      color: #000;
    }
  }

  &__geo {
    position: relative;
    display: table;
    margin: 30px 50px 0 50px;
    font-family: 'Roboto', sans-serif;
    font-size: 18px;
    font-weight: 400;
    color: #23589e;
    text-decoration: none;

    span {
      display: table-cell;
      text-decoration: underline;
    }

    &:hover {
      color: #e8290c;
    }

    img {
      display: table-cell;
      position: absolute;
      left: -20px;
      width: 15px;
      height: 15px;
      text-decoration: none;
    }
  }

  &__btn {
    position: relative;
    margin: 10px 0 20px 50px;
    display: inline-block;
    font-family: 'Roboto', sans-serif;
    font-size: 18px;
    font-weight: 300;
    color: #fff;
    padding: 12px 40px;
    text-decoration: none;
    background-color: #1d609b;
    border-radius: 5px;

    &:hover {
      background-color: #e8290c;
    }

    &:active {
      background: #d0250c;
    }

  }

}